import './service.scss';
import {useState} from "react";
import Site from '../../../assets/images/developer.png';
import Game from '../../../assets/images/game-developer.png';
import UI_UX from '../../../assets/images/ui-ux.png';
import Blockchain from '../../../assets/images/blockchain.png';
import Mobile from '../../../assets/images/mobile-developer.png';
import SMM from '../../../assets/images/smm.png';

const Service = () => {
    const [activeIndex, setActiveIndex] = useState(1);

    return (
        <div className='service'>
            <div className="container">
                <div className="service-inner">
                    <div className="service-item">
                        <div><span>Разработка сайтов</span></div>
                        <div><img src={Site} alt=""/></div>
                    </div>
                    <div className="service-item">
                        <div><img src={Game} alt=""/></div>
                        <div><span>Разработка игр</span></div>
                    </div>
                    <div className="service-item">
                        <div><span>UI/UX Дизайн</span></div>
                        <div><img src={UI_UX} alt=""/></div>
                    </div>
                    <div className="service-item">
                        <div><img src={Blockchain} alt=""/></div>
                        <div><span>Разработка Blockchain</span></div>
                    </div>
                    <div className="service-item">
                        <div><span>Мобильные  приложенние</span></div>
                        <div><img src={Mobile} alt=""/></div>
                    </div>
                    <div className="service-item">
                        <div><img src={SMM} alt=""/></div>
                        <div><span>Маркетинг SMM</span></div>
                    </div>
                </div>
                <div className="service-list">
                    <div className="service-list-item" onMouseEnter={() => setActiveIndex(1)}>
                        <span>Мобильные  приложенние</span>
                    </div>
                    <div className="service-list-item" onMouseEnter={() => setActiveIndex(2)}>
                        <span>Разработка Blockchain</span>
                    </div>
                    <div className="service-list-item" onMouseEnter={() => setActiveIndex(3)}>
                        <span>Разработка сайтов</span>
                    </div>
                    <div className="service-list-item" onMouseEnter={() => setActiveIndex(4)}>
                        <span>UI/UX Дизайн</span>
                    </div>
                    <div className="service-list-item" onMouseEnter={() => setActiveIndex(5)}>
                        <span>Разработка игр</span>
                    </div>
                    <div className="service-list-item" onMouseEnter={() => setActiveIndex(6)}>
                        <span>Маркетинг SMM</span>
                    </div>

                    <div className="service-list-images">
                        {activeIndex === 1 && (
                            <div className="service-list-photo">
                                <img src={Mobile} alt=""/>
                            </div>
                        )}
                        {activeIndex === 2 && (
                            <div className="service-list-photo">
                                <img src={Blockchain} alt=""/>
                            </div>
                        )}
                        {activeIndex === 3 && (
                            <div className="service-list-photo">
                                <img src={Site} alt=""/>
                            </div>
                        )}
                        {activeIndex === 4 && (
                            <div className="service-list-photo">
                                <img src={UI_UX} alt=""/>
                            </div>
                        )}
                        {activeIndex === 5 && (
                            <div className="service-list-photo">
                                <img src={Game} alt=""/>
                            </div>
                        )}
                        {activeIndex === 6 && (
                            <div className="service-list-photo">
                                <img src={SMM} alt=""/>
                            </div>
                        )}
                    </div>
                </div>
            </div>
        </div>
    )

}

export default Service;