import './video.scss';
import Logo from '../../../assets/images/logo_des.png'

const Video = () => {

    return (
        <div className='video'>
            <div className="video-inner">
                <div className="video-title title">WHAT IS QBERG?</div>
                <div className="video-logo">
                    <img src={Logo} alt=""/>
                </div>
            </div>
        </div>
    )
}

export default Video;