import './team.scss';
import {useState} from "react";
import TeamMember from '../../../assets/images/team-member-1.png'

const Team = () => {
    const [activeIndex, setActiveIndex] = useState(1)

    return (
        <div className='team'>
            <div className="container">
                <div className="team-title title">WORK AS TEAM</div>
                <div className="team-members">

                    <div className="team-members-item"
                        onMouseEnter={()=>setActiveIndex(1)}
                        onClick={()=>setActiveIndex(1)}
                    >
                        <div
                            className={activeIndex === 1 ? "team-members-work team-members-work_active" : "team-members-work"}>
                            SMM
                        </div>
                        {activeIndex === 1 && (
                            <div className="team-members-info">
                                <div className="team-members-name">MISHA</div>
                                <div className="team-members-photo">
                                    <img src={TeamMember} alt=""/>
                                </div>
                                <div className="team-members-position">
                                    <p>111.Net</p>
                                    <p>SMM </p>
                                    <p>SMM </p>
                                </div>
                            </div>
                        )}
                    </div>

                    <div className="team-members-item"
                         onMouseEnter={()=>setActiveIndex(2)}
                         onClick={()=>setActiveIndex(2)}
                    >
                        <div
                            className={activeIndex === 2 ? "team-members-work team-members-work_active" : "team-members-work"}>
                            DEV OPS
                        </div>
                        {activeIndex === 2 && (
                            <div className="team-members-info">
                                <div className="team-members-name">MISHA</div>
                                <div className="team-members-photo">
                                    <img src={TeamMember} alt=""/>
                                </div>
                                <div className="team-members-position">
                                    <p>222.Net</p>
                                    <p>  DEV OPS </p>
                                    <p>  DEV OPS </p>
                                </div>
                            </div>
                        )}
                    </div>

                    <div className="team-members-item"
                         onMouseEnter={()=>setActiveIndex(3)}
                         onClick={()=>setActiveIndex(3)}
                    >
                        <div
                            className={activeIndex === 3 ? "team-members-work team-members-work_active" : "team-members-work"}>
                            CEO
                        </div>
                        {activeIndex === 3 && (
                            <div className="team-members-info">
                                <div className="team-members-name">MISHA</div>
                                <div className="team-members-photo">
                                    <img src={TeamMember} alt=""/>
                                </div>
                                <div className="team-members-position">
                                    <p>333.Net</p>
                                    <p> CEO </p>
                                    <p> CEO </p>
                                </div>
                            </div>
                        )}
                    </div>

                    <div className="team-members-item"
                         onMouseEnter={()=>setActiveIndex(4)}
                         onClick={()=>setActiveIndex(4)}
                    >
                        <div
                            className={activeIndex === 4 ? "team-members-work team-members-work_active" : "team-members-work"}>
                            DESIGN
                        </div>
                        {activeIndex === 4 && (
                            <div className="team-members-info">
                                <div className="team-members-name">MISHA</div>
                                <div className="team-members-photo">
                                    <img src={TeamMember} alt=""/>
                                </div>
                                <div className="team-members-position">
                                    <p>444.Net</p>
                                    <p>   DESIGN </p>
                                    <p>   DESIGN </p>
                                </div>
                            </div>
                        )}
                    </div>

                    <div className="team-members-item"
                         onMouseEnter={()=>setActiveIndex(5)}
                         onClick={()=>setActiveIndex(5)}
                    >
                        <div
                            className={activeIndex === 5 ? "team-members-work team-members-work_active" : "team-members-work"}>
                            Painter
                        </div>
                        {activeIndex === 5 && (
                            <div className="team-members-info">
                                <div className="team-members-name">MISHA</div>
                                <div className="team-members-photo">
                                    <img src={TeamMember} alt=""/>
                                </div>
                                <div className="team-members-position">
                                    <p>555.Net</p>
                                    <p> PAINTER </p>
                                    <p> PAINTER </p>
                                </div>
                            </div>
                        )}
                    </div>

                    <div className="team-members-item"
                         onMouseEnter={()=>setActiveIndex(6)}
                         onClick={()=>setActiveIndex(6)}
                    >
                        <div
                            className={activeIndex === 6 ? "team-members-work team-members-work_active" : "team-members-work"}>
                            PROJECT
                        </div>
                        {activeIndex === 6 && (
                            <div className="team-members-info">
                                <div className="team-members-name">MISHA</div>
                                <div className="team-members-photo">
                                    <img src={TeamMember} alt=""/>
                                </div>
                                <div className="team-members-position">
                                    <p>666.Net</p>
                                    <p> PROJECT </p>
                                    <p> PROJECT </p>
                                </div>
                            </div>
                        )}
                    </div>

                </div>
            </div>
        </div>
    )

}

export default Team;
