import './smoke.scss';
import {useEffect, useRef} from "react";
import * as THREE from 'three';
import SmokePhoto from '../../assets/images/smoke.png';
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls'

const Smoke = ({styles = {}}) => {
    const blockRef = useRef(null);
    const canvasRef = useRef(null);

    useEffect(() => {
        let scene, camera, renderer, cloudParticles = [], controls;
        let canvas = canvasRef.current;

        let sizes = {
            width: canvas.offsetWidth,
            height: canvas.offsetHeight
        }

        function init() {
            scene = new THREE.Scene();
            camera = new THREE.PerspectiveCamera(60, sizes.width / sizes.height, 1, 1000);
            camera.position.set(1.16, -0.12, 1)
            camera.rotation.set(1.16, -0.12, 0.09)

            let ambient = new THREE.AmbientLight(0xffffff);
            scene.add(ambient);
            let directionalLight = new THREE.DirectionalLight(0xffffff);
            directionalLight.position.set(0, 0, 1);
            scene.add(directionalLight)

            renderer = new THREE.WebGLRenderer({
                alpha: true,
                canvas
            });
            // renderer.setSize(sizes.width, sizes.height);

            // let axesHelper = new THREE.AxesHelper()
            // scene.add(axesHelper)

            scene.fog = new THREE.FogExp2(0xcccccc, 0.001);
            // renderer.setClearColor(scene.fog.color);
            renderer.setClearColor(0xffffff, 0);
            blockRef.current.appendChild(renderer.domElement)

            let loader = new THREE.TextureLoader();
            loader.load(SmokePhoto, (texture) => {
                let cloudGeo = new THREE.PlaneBufferGeometry(500, 500);
                let cloudMaterial = new THREE.MeshLambertMaterial({
                    map: texture,
                    transparent: true
                })

                for (let p = 0; p < 100; p++) {
                    let cloud = new THREE.Mesh(cloudGeo, cloudMaterial);
                    cloud.position.x = (-1) ** p * Math.random() * 2000 ;
                    cloud.position.y = 650;
                    cloud.position.z = Math.random() * 350 - 550;
                    cloud.rotation.set(1.16, -0.12, 50+Math.random() * 2 * Math.PI)
                    cloud.material.opacity = 0.55;
                    cloudParticles.push(cloud)
                    scene.add(cloud)
                }
            })

            // controls = new OrbitControls( camera, renderer.domElement );
            // controls.update();

            render()
        }

        window.addEventListener('resize', () => {
            sizes.width = window.innerWidth
            sizes.height = canvas.offsetHeight
            camera.aspect = sizes.width / sizes.height;
            camera.updateProjectionMatrix();
            renderer.setSize(sizes.width, sizes.height);
        })

        function render() {
            cloudParticles.forEach(p => p.rotation.z -= 0.01)
            renderer.render(scene, camera)
            requestAnimationFrame(render)
            // controls.update();
        }

        init()

        return () => {
            cancelAnimationFrame(render)
        }
    }, [])

    return (
        <div ref={blockRef} className='smoke' style={styles}>
            <canvas ref={canvasRef}>Canvas not supported</canvas>
        </div>
    )

}

export default Smoke;