import './footer.scss';
import logo from '../../assets/images/logo_min.png';
import tel from '../../assets/images/tel.png';
import message from '../../assets/images/message.png';
import instagram from '../../assets/images/instagram.png';
import linkedIn from '../../assets/images/linkedin.png';
import facebook from '../../assets/images/facebook.png';
import telegram from '../../assets/images/telegram.png';

const Footer = () => {

    return (
        <footer className='footer'>
            <div className="container">
                <div className="footer-inner">

                    <div className="footer-links">
                        <div className="footer-links-logo">
                            <a href="#"><img src={logo} alt="logo"/></a>
                        </div>
                        <div className="footer-links-contact">
                            <p><img src={message} alt="message"/> info@brain .ua</p>
                            <p><img src={tel} alt="message"/> +38(050) 453 - 29 - 99</p>
                        </div>
                        <ul className="footer-links-social">
                            <li><a href="#"><img src={facebook} alt=""/></a></li>
                            <li><a href="#"><img src={instagram} alt=""/></a></li>
                            <li><a href="#"><img src={linkedIn} alt=""/></a></li>
                            <li><a href="#"><img src={telegram} alt=""/></a></li>
                        </ul>
                    </div>

                    <div className="footer-contact">
                        <p><img src={tel} alt="message"/> +38(050) 453 - 29 - 99</p>
                        <p><img src={message} alt="message"/> info@brain .ua</p>
                    </div>

                    <div className="footer-nav">
                        <ul>
                            <li><a href="#">Услуги</a></li>
                            <li><a href="#">Портфолио</a></li>
                            <li><a href="#">Блог</a></li>
                            <li><a href="#">Команда</a></li>
                        </ul>

                        <ul>
                            <li><a href="#">Дизайн</a></li>
                            <li><a href="#">Маркетинг</a></li>
                            <li><a href="#">Разработка</a></li>
                            <li><a href="#">Разработка Blockchain</a></li>
                        </ul>
                    </div>
                </div>
            </div>
        </footer>
    )
}

export default Footer;