import './menu-burger.scss';
import burger from '../../assets/images/burger.png'
import {useState} from "react";

const MenuBurger = () => {
    const [isOpen, setIsOpen] = useState(false)

    return (
        <div className='menu'>
            <div className="container">
                <div className="menu-inner">
                    <span className='menu-burger' onClick={() => setIsOpen(true)}>
                        <img src={burger} alt=""/>
                    </span>

                    <span className="menu-burger_des" onClick={() => setIsOpen(!isOpen)}>
                        <img src={burger} alt=""/>
                    </span>


                    <div className={isOpen ? "menu-list menu-open" : "menu-list"}>
                        <span className="menu-close" onClick={() => setIsOpen(false)}>
                            <i className="fas fa-times"/>
                        </span>

                        <ul>
                            <li><a href="#">Портфолио</a></li>
                            <li><a href="#">Услуги</a></li>
                            <li><a href="#">Команда</a></li>
                            <li><a href="#">Блог</a></li>
                            <li><a href="#">Ваканции</a></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default MenuBurger;